html, body, #root {
    height: 100%;
}

main {
    display: flex;
    height: 100%;
    position: relative;
}

.player-info {
    font-size: 30px;
    text-align: center;
    margin-top: 278px;
    margin-left: 50px;
}

.steps {
}
.step {
    width: 238px;
    height: 50px;
    border: 1px solid black;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}
.step_next {
    border: 2px dashed darkblue;
}
.step_last {
}
.step-item {
    width: 50px;
    height: 50px;
    line-height: 50px;
}

.action {
    width: 80px;
    height: 80px;
}

.slot-panel {
    height: 40px;
    display: flex;
}
.slot {
    width: 46px;
    height: 46px;
    border: 1px solid black;
}

.logs {
    overflow: auto;
    flex: 1;
    width: 240px;
}

.played-steps {
    display: flex;
    width: 250px;
    margin-top: 50px;
}
.played-step {
    min-width: 40px;
    min-height: 40px;
    padding: 0 15px;
    display: flex;
    justify-content: center;
    align-items: center;
}